html{
  box-sizing: border-box;
}

*, *::before, *::after{
  box-sizing: inherit;
}

.header{
  max-width: 1280px;
  min-width: 345px;
  min-height: 24px;
  background-color: black;
  margin: 0 auto;
}

.top_bar{
  max-width: 1110px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
}

.phone{
  display: inline;
  margin-right: 5px;
  opacity: 0.8;
}

.nomer{
  display: inline-block;
  font-family: Spartan;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 20px;
  margin-right: 10px;

  color: #FFFFFF;

  opacity: 0.8;
}

.vector{
  display: inline;
  margin-right: 5px;
  opacity: 0.8;
}

.location{
  display: inline;
  font-family: Inter;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 20px;
  letter-spacing: 0.03em;
  margin-right: 10px;

  color: #FFFFFF;

  opacity: 0.8;
}

.clock{
  display: inline;
  margin-right: 5px;
  opacity: 0.8;
}

.time{
  display: inline;
  font-family: Spartan;
  font-style: normal;
  font-weight: 500;
  font-size: 11px;
  line-height: 20px;

  color: #FFFFFF;

  opacity: 0.8;
}

.group_1{
  display: flex;
  flex-wrap: wrap;
  text-align: center;
}

.group_2{
  display: inline-block;
}

.logo{
  margin-right: 10px;
}

.nav{
  max-width: 1110px;
  min-height: 66px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
  padding-bottom: 25px;
}
.nav_logo{
  font-family: Playfair Display SC;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  color: #121212;
  text-decoration: none;
  margin-right: 90px;
  vertical-align: middle;
}

.nav_menu{
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  color: #121212;
  text-decoration: none;
  margin-right: 30px;
}

@media (max-width: 850px){
  .nav_menu{
      max-height: 0;
      background: white;
      float: none;
      position: absolute;
      overflow: hidden;
      top: 63px;
      right: 0;
      left: 0;
      margin: 0;
      padding: 0;
      z-index: 3;
  }
}

.content__banners{
  max-width: 1110px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
  margin: 0 auto;
}

@media (max-width: 1110px){
  .content__banners{
    grid-template-columns: repeat(1, 1fr);
  }
}

.content__banner__grid{
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 26px;
}

.content__banner__sm__grid{
  max-width: 540px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 30px;
}

.banner{
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

.ban_1{
  max-height: 490px;
}

.ban_2{
  max-height: 200px;
}

.ban_3{
  max-height: 200px;
}

.ban_4{
  max-height: 260px;
}

/* advantage start */

.advantage{
  max-width: 1281px;
  height: 103px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 0 85px;
  margin: 0 auto;
}

@media (max-width: 1110px){
  .advantage{
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 800px){
  .advantage{
    grid-template-columns: repeat(1, 1fr);
  }
}

.advantage__group{
  margin: 32px 0;
  display: flex;
  flex-wrap: wrap;
}

.advantage__group__pic{
  margin-right: 8px;
}

.advantage__group__text__lg{
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 13px;
  display: flex;
  align-items: center;
  letter-spacing: 0.06em;
}

.advantage__group__text__sm{
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  color: #121212;
  opacity: 0.9;
}